'use client';

import React from 'react';
import {
    LineChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer
} from 'recharts';

interface PestData {
    createdAt: string;
    count: number;
    pestType: string;
}

interface PestTrendChartProps {
    data: PestData[];
}

const PestTrendChart: React.FC<PestTrendChartProps> = ({ data }) => {
    // Format data for Recharts: group by date or just use the raw data if it's already sorted
    const chartData = data.map(item => ({
        time: new Date(item.createdAt).toLocaleDateString(),
        count: item.count,
        type: item.pestType
    }));

    return (
        <div className="w-full h-[400px] bg-[#0f172a] p-6 rounded-2xl shadow-sm border border-slate-800">
            <h3 className="text-xl font-semibold text-slate-100 mb-6">Pest Detection Trends</h3>
            <ResponsiveContainer width="100%" height="100%">
                <LineChart
                    data={chartData}
                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
                >
                    <CartesianGrid strokeDasharray="3 3" stroke="#1e293b" />
                    <XAxis
                        dataKey="time"
                        stroke="#94a3b8"
                        fontSize={12}
                        tickLine={false}
                        axisLine={false}
                    />
                    <YAxis
                        stroke="#94a3b8"
                        fontSize={12}
                        tickLine={false}
                        axisLine={false}
                    />
                    <Tooltip
                        contentStyle={{
                            backgroundColor: '#1e293b',
                            borderRadius: '12px',
                            border: '1px solid #334155',
                            boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.5)'
                        }}
                        itemStyle={{ color: '#fff' }}
                        labelStyle={{ color: '#94a3b8' }}
                    />
                    <Legend verticalAlign="top" height={36} />
                    <Line
                        type="monotone"
                        dataKey="count"
                        name="Pest Count"
                        stroke="#10b981"
                        strokeWidth={3}
                        dot={{ fill: '#10b981', strokeWidth: 2, r: 4 }}
                        activeDot={{ r: 6, strokeWidth: 0 }}
                    />
                </LineChart>
            </ResponsiveContainer>
        </div>
    );
};

export default PestTrendChart;
