'use client';

import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { useEffect, useState, useCallback } from 'react';
import { DataService } from '@/utils/api';
import PestTrendChart from '@/components/Dashboard/PestTrendChart';
import ImageUpload from '@/components/Dashboard/ImageUpload';

// --- Interfaces for TypeScript Safety ---
interface SensorData {
    temperature: number;
    humidity: number;
    soilMoisture: number;
    riskLevel: string;
}

interface PestData {
    pestType: string;
    count: number;
    confidence: number;
    severity: string;
    reasoning?: string;
    recommendation?: string;
    createdAt: string;
}

export default function Home() {
    const [pestData, setPestData] = useState<PestData[]>([]);
    const [sensorData, setSensorData] = useState<SensorData[]>([]);
    const [loading, setLoading] = useState(true);

    // üîÑ Data Fetching Logic
    const fetchData = useCallback(async () => {
        try {
            const [pests, sensors] = await Promise.all([
                DataService.getPestDetections(),
                DataService.getSensorData()
            ]);
            setPestData(pests);
            setSensorData(sensors);
        } catch (err) {
            console.error('Connection to backend failed');
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    // üìÑ PDF Report Generator
    const downloadPDF = () => {
        const doc = new jsPDF();
        doc.setFontSize(18);
        doc.text("ExpelAI - Agricultural Field Report", 14, 22);
        doc.setFontSize(10);
        doc.setTextColor(100);
        doc.text(`Generated: ${new Date().toLocaleString()}`, 14, 30);

        const tableRows = pestData.map(p => [
            new Date(p.createdAt).toLocaleDateString(),
            p.pestType,
            p.count,
            p.severity || 'Low',
            p.recommendation || 'Standard Monitoring'
        ]);

        autoTable(doc, {
            startY: 40,
            head: [['Date', 'Pest Detected', 'Count', 'Risk Level', 'Recommendation']],
            body: tableRows,
            theme: 'striped',
            headStyles: { fillColor: [16, 185, 129] }
        });

        doc.save("ExpelAI_Field_Report.pdf");
    };

    if (loading) return (
        <div className="flex min-h-screen items-center justify-center bg-[#020617] text-emerald-500 font-bold">
            BOOTING EXPEL AI SYSTEMS...
        </div>
    );

    const latest = sensorData[sensorData.length - 1] || { temperature: 24, humidity: 65, soilMoisture: 42, riskLevel: 'Low' };

    return (
        <div className="min-h-screen bg-[#020617] text-slate-200 p-6 md:p-12 font-sans">

            {/* ‚ö†Ô∏è GLOBAL ALERT BANNER */}
            {pestData.some(p => p.severity === 'High') && (
                <div className="bg-red-500/10 border border-red-500/50 p-3 mb-8 rounded-xl text-center animate-pulse">
                    <p className="text-red-500 text-xs font-black tracking-widest uppercase">
                        ‚ö†Ô∏è Alert: High-Risk Infestation Detected in Polyhouse ‚ö†Ô∏è
                    </p>
                </div>
            )}

            <div className="max-w-6xl mx-auto">
                {/* HEADER */}
                <header className="flex justify-between items-center mb-12 border-b border-slate-800 pb-8">
                    <div>
                        <h1 className="text-3xl font-black text-white tracking-tighter">EXPEL<span className="text-emerald-500">AI</span></h1>
                        <p className="text-slate-500 text-sm font-medium">AI-Powered Polyhouse Monitoring</p>
                    </div>
                    <button
                        onClick={downloadPDF}
                        className="bg-emerald-600 hover:bg-emerald-500 text-white px-6 py-3 rounded-2xl font-bold text-xs shadow-lg shadow-emerald-900/20 transition-all"
                    >
                        üì• DOWNLOAD PDF REPORT
                    </button>
                </header>

                <main className="space-y-10">

                    {/* 1. SENSOR DATA CARDS */}
                    <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                        <StatCard label="Air Temp" value={`${latest.temperature}¬∞C`} color="emerald" />
                        <StatCard label="Humidity" value={`${latest.humidity}%`} color="blue" />
                        <StatCard label="Soil Moisture" value={`${latest.soilMoisture}%`} color="cyan" />
                        <StatCard label="Env Risk" value={latest.riskLevel} color={latest.riskLevel === 'High' ? 'red' : 'emerald'} />
                    </div>

                    {/* 2. AI UPLOAD TOOL */}
                    <section>
                        <ImageUpload onUploadSuccess={fetchData} />
                    </section>

                    {/* 3. TREND CHART */}
                    <section className="bg-slate-900/50 border border-slate-800 p-8 rounded-[2.5rem]">
                        <h2 className="text-xl font-bold text-white mb-8">Pest Population Trends</h2>
                        <div className="h-[350px]">
                            <PestTrendChart data={pestData} />
                        </div>
                    </section>

                    {/* 4. ACTIVITY LOG WITH REASONING & RECOMMENDATIONS */}
                    <section className="bg-slate-900/50 border border-slate-800 p-8 rounded-[2.5rem]">
                        <h2 className="text-xl font-bold text-white mb-8">Detailed Field History</h2>
                        <div className="space-y-6 max-h-[600px] overflow-y-auto pr-2 custom-scrollbar">
                            {[...pestData].reverse().map((report, index) => (
                                <div key={index} className="p-6 bg-slate-800/30 border border-slate-700/50 rounded-3xl hover:bg-slate-800/60 transition-all">
                                    <div className="flex justify-between items-start mb-4">
                                        <div>
                                            <div className="flex items-center gap-2 mb-1">
                                                <span className={`w-3 h-3 rounded-full ${report.severity === 'High' ? 'bg-red-500 animate-pulse' : 'bg-emerald-500'}`}></span>
                                                <p className="text-white font-bold text-xl">{report.pestType}</p>
                                            </div>
                                            <p className="text-xs text-slate-500 font-medium">{new Date(report.createdAt).toLocaleString()}</p>
                                        </div>
                                        <div className="text-right">
                                            <p className="text-emerald-400 font-black text-3xl">{report.count}</p>
                                            <p className="text-[10px] text-slate-500 uppercase font-bold tracking-tighter">Total Pests</p>
                                        </div>
                                    </div>

                                    {/* Actionable Insights Section */}
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6 pt-6 border-t border-slate-700/50">
                                        <div>
                                            <p className="text-[10px] text-slate-500 font-black uppercase tracking-widest mb-2">AI Visual Reasoning</p>
                                            <p className="text-slate-300 text-sm italic leading-relaxed">
                                                "{report.reasoning || 'No specific visual reasoning provided by the AI.'}"
                                            </p>
                                        </div>
                                        <div className="bg-emerald-500/5 p-4 rounded-2xl border border-emerald-500/10">
                                            <p className="text-[10px] text-emerald-500 font-black uppercase tracking-widest mb-2">Preventive Recommendation</p>
                                            <p className="text-white text-sm font-medium">
                                                {report.recommendation || 'Continue standard polyhouse climate monitoring.'}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                            {pestData.length === 0 && (
                                <p className="text-center text-slate-500 py-12">No field data recorded yet. Upload an image to start.</p>
                            )}
                        </div>
                    </section>
                </main>
            </div>
        </div>
    );
}

// Stat Card Sub-Component
function StatCard({ label, value, color }: { label: string, value: string, color: 'emerald' | 'blue' | 'cyan' | 'red' }) {
    const textColors = { emerald: "text-emerald-400", blue: "text-blue-400", cyan: "text-cyan-400", red: "text-red-400" };
    const borders = { emerald: "border-emerald-500/20", blue: "border-blue-500/20", cyan: "border-cyan-500/20", red: "border-red-500/20" };

    return (
        <div className={`bg-slate-900/40 border ${borders[color]} p-6 rounded-3xl transition-all hover:-translate-y-1`}>
            <p className="text-[10px] font-black text-slate-500 uppercase tracking-widest mb-3">{label}</p>
            <p className={`text-3xl font-black ${textColors[color]}`}>{value}</p>
        </div>
    );
}